body {
    font-family: Arial, sans-serif;
    font-size: 1rem;
    background-image: linear-gradient(60deg, rgba(106, 105, 129, 0.8), rgba(241,233,233,0.4));
    background-size: contain;
    background-attachment: fixed;
    background-repeat: repeat;
    width: 100%;
    overflow-x: hidden;
    color: #3b3b3b;
  
  }
  
  .container {
    width: 70%;
    margin: 0 auto;
    padding: 2rem;
    border-radius: 8px;
  }
  
  label {
      display: block;
      margin-bottom: 10px;
      color: #636363;
      font-size: 1.2rem;
  }
  
  select {
      height: 2.5rem;
      line-height: 0.2rem;
      padding: 8px;
      margin-bottom: 20px;
      background: #e6e6e6;
      color: #3b3b3b;
      transition: all 0.3s ease-in-out;
      outline: none;
      font-size: 14px;
      border: none;
     
      width: 100%;
      box-shadow: 0 12px 10px -10px rgba(0, 0, 0, 0.2);
      transition-duration: 0.3s;
      letter-spacing: 1.5px;
  }
  
  ul {
      list-style: none;
      padding: 0;
      font-size: 1.2rem;
  }
  
   li {
    border: 1px solid #979797;
    margin-bottom: 1rem;
    background: transparent;
    padding: 10px;
    color: #636363;
  
      }
  
      li:hover {
        background: rgba(255,255,255,0.1);
        color: white;
       
  }